.Header-component {
    min-height: 8vh;
    background-color:#242424;
    text-align: right;
    position: sticky;
    top: 0;
    z-index: 100;
    
}

button {
    color: white;
    background-color: transparent;
    border: none;
    font-weight: bold;
    font-size: 20px;
    margin-top: 2vh;
    margin-right: 30px;
}

@media (max-width: 600px) {
    button {
        font-size: 12px;
        margin-right: 5px;
    }
}